$sidebar-menu-item-active-color: var(--#{$prefix}sidebar-menu-item-active-color);
$sidebar-menu-item-active-effact-color: var(--#{$prefix}sidebar-menu-item-active-effact-color);
$sidebar-border-color: var(--#{$prefix}sidebar-border-color);
$sidebar-alert: var(--#{$prefix}sidebar-alert);
$sidebar-width: 250px;
$sidebar-collapsed-width: 70px;
$sidebar-width-sm: 160px;

$sidebar-dark-bg: var(--#{$prefix}sidebar-dark-bg);
