// Topbar - Deafult Light
$header-height: 70px;
$header-bg: $white;
$header-item-color: var(--#{$prefix}header-item-color);

// Topbar - Dark
$header-dark-bg: #313533;
$header-dark-item-color: #e9ecef;

// Topbar Search
$topbar-search-bg: var(--#{$prefix}topbar-search-bg);
$search-width: var(--#{$prefix}search-width);
$search-border-color: var(--#{$prefix}search-border-color);
$topbar-dark-search-bg: var(--#{$prefix}topbar-dark-search-bg);

// Footer
$footer-height: 60px;
$footer-bg: var(--#{$prefix}footer-bg);
$footer-color: var(--#{$prefix}footer-color);

// Horizontal nav
$topnav-bg: var(--#{$prefix}topnav-bg);
$menu-item-color: var(--#{$prefix}menu-item-color);
$menu-item-active-color: var(--#{$prefix}menu-item-active-color);

// Horizontal nav
$topnav-dark-bg: #363a38;
$menu-dark-item-color: #99a4b1;
$menu-dark-item-active-color: #fff;

// Right Sidebar
$rightbar-width: 300px;

// Display
$display-none: none;
$display-block: block;

// Brand 
$navbar-brand-box-width: 250px;

// Boxed layout width
$boxed-layout-width: 1300px;
$boxed-body-bg: var(--#{$prefix}boxed-body-bg);

// apex charts
$apex-grid-color: var(--#{$prefix}apex-grid-color);

// table
$table-head-bg: $gray-100;
$table-dark-border-color: tint-color($gray-800, 7.5%);

